  //This code is commented as all options are already declared above and it doesn't add anything else 
  public async onClickSegment(selectedSegment) {
    await this.loadBeneficiaries(selectedSegment);
  }

  /**
   * fetching beneficiaries
   * @param segment 
   */
  async loadBeneficiaries(segment) {
    this.requestWasSent = true;
    this.selectedSegment = segment;
    if (segment === this.internalBeneficiaryKey) {
      this.beneficiariesRequestParam = {
        trxTypeParameterName: CommonBussinessConstant.INTERNAL_BENEFICIARY_TYPE,
        filterByOper: true,
        operId: CommonBussinessConstant.INTERNAL_BENEFICIARY_OPER_ID
      };
      await this.returnInternalBeneficiaries(this.beneficiariesRequestParam);
    } else if (segment === this.localBeneficiaryKey) {
      this.beneficiariesRequestParam = {
        trxTypeParameterName: CommonBussinessConstant.LOCAL_BENEFICIARY_TYPE,
        filterByOper: true,
        operId: CommonBussinessConstant.LOCAL_BENEFICIARY_OPER_ID
      };
      await this.returnLocalBeneficiaries(this.beneficiariesRequestParam);
    } else if (segment === this.internationalBeneficiaryKey) {
      this.beneficiariesRequestParam = {
        trxTypeParameterName: CommonBussinessConstant.INTERNATIONAL,
        filterByOper: true,
        operId: CommonBussinessConstant.EXTERNAL_BENEFICIARY_OPER_ID
      };
      await this.returnInternationalBeneficiaries(this.beneficiariesRequestParam);
    }
    else if (segment === CommonBussinessConstant.BANK_CARD_BENEFICIARY_KEY) {
      this.beneficiariesRequestParam = {
        trxTypeParameterName: CommonBussinessConstant.BANK_CARD_BENEFICIARY_TYPE,
        filterByOper: true,
        operId: CommonBussinessConstant.BANK_CARD_BENEFICIARY_OPER_ID
      };
      await this.returnBankCardBeneficiaries(this.beneficiariesRequestParam);
    } else if (segment === CommonBussinessConstant.LOCAL_CARD_BENEFICIARY_KEY) {
      this.beneficiariesRequestParam = {
        trxTypeParameterName: CommonBussinessConstant.LOCAL_CARD_BENEFICIARY_TYPE,
        filterByOper: true,
        operId: CommonBussinessConstant.LOCAL_CARD_BENEFICIARY_OPER_ID
      };
      await this.returnLocalCardBeneficiaries(this.beneficiariesRequestParam);
    }
    else {
      this.beneficiariesRequestParam = {
        trxTypeParameterName: CommonBussinessConstant.CASH_TRANSFER,
        filterByOper: true,
        operId: CommonBussinessConstant.CASH_BENEFICIARY_OPER_ID
      };
      await this.returnCashTransfers(this.beneficiariesRequestParam);
    }
    this.requestWasSent = false;
  }

  /**
   * fetching internal beneficiaries
   * @param requestData
   */
  private async returnInternalBeneficiaries(requestData: IOmniBeneficiaryRequest) {
    const result = await this.omniPull.returnBeneficiaries(requestData).catch(error => {
      this.logger.error('Error: While fetching internal beneficiaries in BeneficiaryListPage :', error);
    });
    if (result && result.gridModel.length > 0) {
      this.noInternalBeneficiariesFound = false;
      this.internalBeneficiaryOptions.listOfOptions = [];
      this.internalBeneficiaryOptions.listOfOptions = this.populateCustomInternalBeneficiaries(result.gridModel);
    } else {
      this.internalBeneficiaryOptions.listOfOptions = []; // this.populateCustomInternalBeneficiaries([]);
      this.noInternalBeneficiariesFound = true;
    }
  }

